const upload = function(self, url, data, type, path, name, _success) {
	if(data == '' || path == ''){
		uni.request({
			url: url,
			method: type,
			data: data,
			header: {
				'Authorization': 'Bearer ' + uni.getStorageSync('token'),
				'content-type': 'application/json;charset=UTF-8'
			},
			success: res => {
				var res = res.data;
				if(res.code == 400){
					uni.showToast({
						icon: 'none',
						title: res.info
					});
					uni.setStorageSync('token','');
					setTimeout(function(){
						uni.reLaunch({
							url: '/pages/login/login'
						});
					},2000);
				}else{
					_success(self, res);
				}
			},
		});
	}else{
		uni.uploadFile({
			url : url,
			filePath: path,
			name: name,
			formData: data,
			header: {
				'Authorization': 'Bearer ' + uni.getStorageSync('token'),
				'content-type': 'application/json;charset=UTF-8'
			},
			success: function (res) {
				res = res.data;
				res = eval('(' + res + ')');
				if(res.code == 400){
					uni.showToast({
						icon: 'none',
						title: res.info
					});
					uni.setStorageSync('token','');
					setTimeout(function(){
						uni.reLaunch({
							url: '/pages/login/login'
						});
					},2000);
				}else{
					_success(self, res);
				}
			}
		});
	}
}
