export default class request {
	constructor(options) {
		//
		this.baseUrl = options.baseUrl || "";
		//
		this.fileUrl = options.fileUrl || "";
		//
		this.header = options.header || {};
		//
		this.config = {
			isPrompt: options.isPrompt === false ? false : true,
			load: options.load === false ? false : true,
			isFactory: options.isFactory === false ? false : true,
			loadMore: options.loadMore === false ? false : true
		};
	}
	
	//header
	getToken(){
		let token = {
			'Authorization':'Bearer '+uni.getStorageSync('token'),
			'content-type': 'application/json;charset=UTF-8',
			/* 'user-form-token' : uni.getStorageSync('user_token'), */
		};
		return token;
	}

	// 
	getDefault(data, options = {}) {
		//console.log(data)
		//url
		let urlType = /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+).)+([A-Za-z0-9-~/])+$/.test(data.url);
		let config = Object.assign({}, this.config, options, data);
		if (data.method == "FILE") {
			config.url = urlType ? data.url : this.fileUrl + data.url;
		} else {
			config.url = urlType ? data.url : this.baseUrl + data.url;
		}
		//
		if (options.header) {
			config.header = Object.assign({}, this.header, options.header);
		} else if (data.header) {
			config.header = Object.assign({}, this.header, data.header);
		} else {
			config.header = this.header;
		}
		//console.log(config)
		return config;
	}

	//post
	post(url = '', data = {}, options = {}) {
		return this.request({
			method: "POST",
			data: data,
			url: url,
			...options
		});
	}

	//get
	get(url = '', data = {}, options = {}) {
		return this.request({
			method: "GET",
			data: data,
			url: url,
			...options
		});
	}

	//put
	put(url = '', data = {}, options = {}) {
		return this.request({
			method: "PUT",
			data: data,
			url: url,
			...options
		});
	}

	//
	upload(url = '', path = '', name = '', data = {}, options = {}) {
		return this.uploadFile({
			url : url,
			filePath: path,
			name: name,
			formData: data,
			...options
		});
	}

	//delete
	delete(url = '', data = {}, options = {}) {
		return this.request({
			method: "DELETE",
			data: data,
			url: url,
			...options
		});
	}

	//
	request(data) {
		return new Promise((resolve, reject) => {
			if (!data.url) {
				//console.log("requesturl");
				reject({
					errMsg: "url",
					statusCode: 0
				});
				return;
			}
			let requestInfo = this.getDefault(data);
			//
			if (this.requestStart) {
				let requestStart = this.requestStart(requestInfo);
				if (typeof requestStart == "object") {
					requestInfo.data = requestStart.data;
					requestInfo.header = requestStart.header;
					requestInfo.isPrompt = requestStart.isPrompt;
					requestInfo.load = requestStart.load;
					requestInfo.isFactory = requestStart.isFactory;
				} else {
					//
					_this.requestEnd && _this.requestEnd(requestInfo, {
						errMsg: "",
						statusCode: 0
					});
					reject({
						errMsg: "",
						statusCode: 0
					});
					return;
				}
			}
			let requestData = {
				url: requestInfo.url,
				header: requestInfo.header, //
				success: (res) => {
					//
					this.requestEnd && this.requestEnd(requestInfo, res);
					//
					if (requestInfo.isFactory && this.dataFactory) {
						//
						this.dataFactory({
							...requestInfo,
							response: res,
							resolve: resolve,
							reject: reject
						});
					} else {
						resolve(res);
					}
				},
				fail: (err) => {
					//console.log("err");
					//
					this.requestEnd && this.requestEnd(requestInfo, err);
					reject(err);
				}
			};
			//
			if (requestInfo.method) {
				requestData.method = requestInfo.method;
			}
			if (requestInfo.data) {
				requestData.data = requestInfo.data;
			}

			if (requestInfo.timeout) {
				requestData.timeout = requestInfo.timeout;
			}

			if (requestInfo.dataType) {
				requestData.dataType = requestInfo.dataType;
			}

			if (requestInfo.responseType) {
				requestData.responseType = requestInfo.responseType;
			}






			uni.request(requestData);
		});
	}

	//
	uploadFile(data) {
		//console.log(data)
		//return new Promise((resolve, reject) => {
			if (!data.url) {
				//console.log("requesturl");
				reject({
					errMsg: "url",
					statusCode: 0
				});
				return;
			}
			let requestInfo = this.getDefault(data);
			//
			if (this.requestStart) {
				let requestStart = this.requestStart(requestInfo);
				if (typeof requestStart == "object") {
					requestInfo.data = requestStart.data;
					requestInfo.header = requestStart.header;
					requestInfo.isPrompt = requestStart.isPrompt;
					requestInfo.load = requestStart.load;
					requestInfo.isFactory = requestStart.isFactory;
				} else {
					//
					_this.requestEnd && _this.requestEnd(requestInfo, {
						errMsg: "",
						statusCode: 0
					});
					reject({
						errMsg: "",
						statusCode: 0
					});
					return;
				}
			}
			let requestData = {
				url: requestInfo.url,
				filePath:requestInfo.filePath,
				name:requestInfo.name,
				header: requestInfo.header, //
				success: (res) => {
					//
					this.requestEnd && this.requestEnd(requestInfo, res);
					//
					if (requestInfo.isFactory && this.dataFactory) {
						//
						this.dataFactory({
							...requestInfo,
							response: res,
							resolve: resolve,
							reject: reject
						});
					} else {
						resolve(res);
					}
				},
				fail: (err) => {
					//console.log("err");
					//
					this.requestEnd && this.requestEnd(requestInfo, err);
					reject(err);
				}
			};
			//
			if (requestInfo.filePath) {
				requestData.filePath = requestInfo.filePath;
			}
			if (requestInfo.name) {
				requestData.name = requestInfo.name;
			}
			if (requestInfo.formData) {
				requestData.formData = requestInfo.formData;
			}

			if (requestInfo.timeout) {
				requestData.timeout = requestInfo.timeout;
			}

			if (requestInfo.dataType) {
				requestData.dataType = requestInfo.dataType;
			}

			if (requestInfo.responseType) {
				requestData.responseType = requestInfo.responseType;
			}






			return new Promise(func => {
				uni.uploadFile({
					url : requestData.url,
					filePath: requestData.filePath,
					name: requestData.name,
					header:{
						"Authorization":requestData.header.Authorization,
					},
					formData: requestData.formData,
					success: res => {
						res = res.data;
						res = eval('(' + res + ')');
						func(res)
					}
				});
			});
		//});
	}
	
	//jsonp(H5)
	jsonp(url = '', data = {}, options = {}) {
		let requestInfo = this.getDefault({
			method: "JSONP",
			data: data,
			url: url,
		}, options);
		let dataStr = '';
		Object.keys(data).forEach(key => {
			dataStr += key + '=' + data[key] + '&';
		});
		//&
		if (dataStr !== '') {
			dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'));
		}
		requestInfo.url = requestInfo.url + '?' + dataStr;
		const _this = this;
		return new Promise((resolve, reject) => {
			let callbackName = "callback" + Math.ceil(Math.random() * 1000000);
			if (_this.requestStart) {
				requestInfo.data = data;
				let requestStart = _this.requestStart(requestInfo);
				if (typeof requestStart == "object") {
					requestInfo.data = requestStart.data;
					requestInfo.header = requestStart.header;
					requestInfo.isPrompt = requestStart.isPrompt;
					requestInfo.load = requestStart.load;
					requestInfo.isFactory = requestStart.isFactory;
				} else {
					//
					_this.requestEnd && _this.requestEnd(requestInfo, {
						errMsg: "",
						statusCode: 0
					});
					reject({
						errMsg: "",
						statusCode: 0
					});
					return;
				}
			}










			//
			_this.requestEnd && _this.requestEnd(requestInfo, {
				errMsg: "request:ok",
				statusCode: 200
			});
		});
	}
}
